@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;500;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600;700;800&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
/*
Template Name: Opixo - Tailwind CSS Multipurpose Landing Page Template
Version: 1.0
Author: coderthemes
Email: support@coderthemes.com
File: _reboot.scss
*/
html {
  @apply relative scroll-smooth;
}

body {
  @apply font-nunito overflow-x-hidden text-gray-500;
  font-size: 15px;
  -webkit-tap-highlight-color: transparent;
}

p {
  @apply font-maven;
}

h1, h2, h3, h4, h5, h6 {
  @apply font-semibold font-nunito;
}

/*
Template Name: Opixo - Tailwind CSS Multipurpose Landing Page Template
Version: 1.0
Author: coderthemes
Email: support@coderthemes.com
File: _navbar.scss
*/
.navbar {
  @apply font-nunito block fixed top-0 inset-x-0 z-40 w-full lg:bg-transparent bg-white border-b lg:border-none border-gray-200 transition-all duration-500 shadow-xl lg:shadow-none;
}
.navbar nav {
  @apply flex flex-wrap lg:flex-nowrap items-center justify-between py-4 transition-all duration-500;
}
.navbar nav .logo {
  @apply inline-block whitespace-nowrap;
}
.navbar nav .navbar-nav {
  @apply inline-flex flex-col lg:flex-row gap-x-4 lg:items-center justify-center mt-4 lg:mt-0;
}
.navbar nav .navbar-nav .nav-item .nav-link {
  @apply inline-block relative tracking-wide py-1 px-3.5 rounded-md text-base capitalize font-semibold cursor-pointer transition-all duration-300 bg-transparent;
}
.navbar nav .navbar-nav .nav-item .nav-link:active, .navbar nav .navbar-nav .nav-item .nav-link:focus, .navbar nav .navbar-nav .nav-item .nav-link:hover {
  @apply text-primary bg-gray-400/10;
}
.navbar nav .navbar-nav .active .nav-link {
  @apply text-primary bg-gray-400/10;
}
.navbar nav .nav-btn {
  @apply py-2 px-4 rounded-lg inline-flex items-center justify-center text-base font-medium transition-all;
}
.navbar.nav-light .logo .logo-light {
  @apply hidden lg:block;
}
.navbar.nav-light .logo .logo-dark {
  @apply block lg:hidden;
}
.navbar.nav-light .navbar-nav .nav-item .nav-link {
  @apply lg:text-white text-gray-600;
}
.navbar.nav-light .navbar-nav .nav-item.active .nav-link {
  @apply text-primary lg:bg-white/10 bg-gray-400/10;
}
.navbar.nav-light .nav-btn {
  @apply bg-primary text-white lg:bg-white lg:text-primary;
}
.navbar.nav-dark nav .logo .logo-light {
  @apply hidden;
}
.navbar.nav-dark nav .logo .logo-dark {
  @apply block;
}
.navbar.nav-dark .navbar-nav .nav-item .nav-link {
  @apply text-gray-800;
}
.navbar.nav-dark .navbar-nav .nav-item.active .nav-link {
  @apply text-primary bg-gray-400/10;
}
.navbar.nav-dark .nav-btn {
  @apply bg-primary text-white;
}
.navbar.nav-sticky {
  @apply bg-white border-b border-gray-200 transition-all duration-500 shadow-xl;
}
.navbar.nav-sticky nav .logo .logo-light {
  @apply hidden;
}
.navbar.nav-sticky nav .logo .logo-dark {
  @apply block;
}
.navbar.nav-sticky.nav-light .navbar-nav .nav-item .nav-link {
  @apply text-gray-800;
}
.navbar.nav-sticky.nav-light .navbar-nav .nav-item .nav-link:active, .navbar.nav-sticky.nav-light .navbar-nav .nav-item .nav-link:focus, .navbar.nav-sticky.nav-light .navbar-nav .nav-item .nav-link:hover {
  @apply text-primary bg-gray-400/10;
}
.navbar.nav-sticky.nav-light .navbar-nav .nav-item.active .nav-link {
  @apply text-primary bg-slate-400/10;
}
.navbar.nav-sticky.nav-light .nav-btn {
  @apply bg-primary text-white;
}

/*
